  margin-bottom: 20px;
}
.sort_box #sort_content .list_box .list ul li {
  height: 30px;
  font-size: 14px;
  line-height: 14px;
  /* 체크박스 숨기기 */
}
.sort_box #sort_content .list_box .list ul li a {
  font-size: 15px;
  color: #333;
}
.sort_box #sort_content .list_box .list ul li input {
  width: 15px;
  height: 15px;
}
.sort_box #sort_content .list_box .list ul li input:checked ~ label {
  font-weight: bold;
}
.sort_box #sort_content .list_box .list ul li .all_chk,
.sort_box #sort_content .list_box .list ul li .sport_chk,
.sort_box #sort_content .list_box .list ul li .price_chk,
.sort_box #sort_content .list_box .list ul li .gender_chk {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  /* 커스텀 체크박스 만들기 */
}
.sort_box #sort_content .list_box .list ul li .all_chk + label::before,
.sort_box #sort_content .list_box .list ul li .sport_chk + label::before,
.sort_box #sort_content .list_box .list ul li .price_chk + label::before,
.sort_box #sort_content .list_box .list ul li .gender_chk + label::before {
  content: "";
  display: inline-block;
  width: 10px;
  height: 10px;
  border: 1px solid #333;
  margin-right: 0.5em;
}
.sort_box #sort_content .list_box .list ul li .all_chk:checked + label::before,
.sort_box #sort_content .list_box .list ul li .sport_chk:checked + label::before,
.sort_box #sort_content .list_box .list ul li .price_chk:checked + label::before,
.sort_box #sort_content .list_box .list ul li .gender_chk:checked + label::before {
  background-color: #333;
}
.sort_box #sort_content .list_box .list ul li label {
  cursor: pointer;
}
.sort_box #sort_content .list_box .list_color ul {
  width: 150px;
